div.vis-network div.vis-manipulation {
  box-sizing: content-box;
  border-width: 0;
  border-bottom: 1px;
  border-style: solid;
  border-color: #d6d9d8;
  background: #fff;
  background: -moz-linear-gradient(
    top,
    #fff 0,
    #fcfcfc 48%,
    #fafafa 50%,
    #fcfcfc 100%
  );
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0, #fff),
    color-stop(48%, #fcfcfc),
    color-stop(50%, #fafafa),
    color-stop(100%, #fcfcfc)
  );
  background: -webkit-linear-gradient(
    top,
    #fff 0,
    #fcfcfc 48%,
    #fafafa 50%,
    #fcfcfc 100%
  );
  background: -o-linear-gradient(
    top,
    #fff 0,
    #fcfcfc 48%,
    #fafafa 50%,
    #fcfcfc 100%
  );
  background: -ms-linear-gradient(
    top,
    #fff 0,
    #fcfcfc 48%,
    #fafafa 50%,
    #fcfcfc 100%
  );
  background: linear-gradient(
    to bottom,
    #fff 0,
    #fcfcfc 48%,
    #fafafa 50%,
    #fcfcfc 100%
  );
  padding-top: 4px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 28px;
}
div.vis-network div.vis-edit-mode {
